.sidebar {
    background-color: $couleur-1;
    font-family: $font-1;
    height: 100%;
    min-height: $minHeight;
    width: 400px;
    border-radius: $borderRadius;
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 250px 1fr 108px;
    grid-template-areas: 
    "1"
    "2"
    "3";

    .id{
        grid-area: 1;
        text-align: center;
        user-select: none;
        position: relative;


    .idContent{
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 100%;
        

    img{
        border-radius: 50%; 
        box-shadow: 0 0 0 4px $couleur-3, 0 0 0 8px $couleur-4, 0 0 0 12px $couleur-2;
    }
    h3{
        font-size: 1.3rem;
        text-transform: uppercase;
        letter-spacing: .2rem;
        margin-top: 1.5rem;
        color: $couleur-5;
      }
    }
  }

  .navigation{
    grid-area: 2;
    padding: 2rem 1rem;
   
    ul{
        height: 60%;
        display: grid;
        
        li{
            padding: 1rem 0 1rem .4rem;
            font-size: 1.3rem;
            position: relative;
        
        &:hover i {
            transform: translate(3px, 3px);
        }

        a{
            color: $couleur-5;
        }
        .navActive{
            color: $couleur-4;
        }
        i{
            position: absolute;
            font-size: 0.95rem;
            transform: translateY(3px);
            transition: 0.2s ease;
            
        }
        span{
            margin-left: 2.7rem;
        }
        }
    }
  }

  .socialNetwork{
    grid-area: 3;

    ul{
        display: flex;
        width: 100%;
        padding: 0 14px;
        justify-content: space-between;

        li{
            font-size: 1.5rem;
            background:$couleur-5;
            color: $couleur-1;
            border-radius: 50%;
            padding: .5rem .8rem;
            transition: 0.3s;
            cursor: pointer;

            a:visited{
                color: $couleur-1;
            }
            &:nth-child(1){
                padding: .5rem .85rem;
            }
            &:nth-child(4){
                padding: .5rem .75rem;
            }
            &:hover{
                background: $couleur-4;
            }        

        }
    }
    .signature{
        margin-top: 2rem;
        text-align: center;
        letter-spacing: .1rem;
        font-size: .7rem;

        p{
            color: $couleur-5;
        &:hover{
            color: $couleur-4;
        }
        
        }

    }
  }

}


@media screen and (max-width: 850px) {

    .sidebar{
        display: flex;
        min-height: 80px;
        margin-bottom: .2rem;
        width: 100%;
    }
    
    .id, .socialNetwork{
        display: none;
    }
    .navigation{
        width: 100%;
        padding: .1rem 1rem;

        ul{ 
            display: flex; 
            justify-content: space-between; 

            span{
                margin-left: 1.5rem !important;
            }
        }
    }
}


@media screen and (max-width: 580px) {

    .sidebar{
        .navigation ul {
            display: flex;  
            flex-direction: row;  // Aligne les icônes horizontalement
            justify-content: space-around;

            li{
                padding: 1rem 0 !important;
            }
            i{
                font-size: 1.4rem !important;
                transform: translateY(-3px) !important;
            }
            span{
                display: none;
            }
        }
    }

}