@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@800&family=Josefin+Sans:wght@100&family=Nova+Round&display=swap');

$font-1: 'Nova Round', cursive;
$font-2: 'Dosis', sans-serif;
$font-3: 'Josefin Sans', serif;

$couleur-1: #2a1d52;
$couleur-2: #6c77f4;
$couleur-3: rgb(99, 164, 233);
$couleur-4: #4fedd2;
$couleur-5: #36d0ea;
$white: #ddd;
$black: #333;

$borderRadius: 25px;
$shadow: 0 1px 3px #333;
$minHeight: 95vh;


@mixin verticalCenter {
    display: flex;
    justify-content: center;
    align-items: center; 
  }
  * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

body{
    margin: 1rem auto;
    max-width: 1400px;
    font-family: $font-1;
    color: $black
}

button{
    cursor: pointer;
}
 
li {
  list-style-type: none;
}
a {
  text-decoration: none;
  color: $black;
  cursor: pointer;
}
a:visited {
  color:$black;
}
.home, .knowledges, .portfolio, .contact {
  display: flex; 

  @media screen and (max-width: 1000px){
        display: block !important;       
    }
}

.homeContent, .knowledgesContent, .contactContent, .portfolioContent, .notFoundContent{
    margin-left: .4rem;
    @media screen and (max-width: 1000px){
        margin-left: block !important;       
    }
}

